<template>
  <div class="bulk-details">
    <top-navigation
      v-if="isShowNavigation"
      :previousPage="$t('BUC.taskPlan.bulkTask')"
      :currentPage="$t('BUC.taskPlan.bulkTaskView')"
    ></top-navigation>
    <div class="project-detail" v-loading="!outSideTableData.length">
      <!-- title -->
      <portal-header :projectObj="titleConfig">
        <div slot="title-suffix" class="version-box">
          <span :style="showStatus(bulkStatusMap[bulkHistoryDetail.bulkStatus])">
            {{ bulkStatusMap[bulkHistoryDetail.bulkStatus] }}
          </span>
        </div>
      </portal-header>
      <el-card class="card1">
        <div class="text item">
          <el-descriptions
            :column="descriptionsColumn"
            direction="vertical"
            :colon="false"
            :label-style="labelStyle"
            :content-style="contentStyle"
          >
            <el-descriptions-item :label="$t('BUC.taskHistory.planName')">{{
              bulkHistoryDetail.planName | formatEmpty
            }}</el-descriptions-item>
            <el-descriptions-item :label="$t('BUC.taskPlan.planID')">{{
              bulkHistoryDetail.planID | formatEmpty
            }}</el-descriptions-item>
            <el-descriptions-item :label="$t('BUC.taskHistory.bulkTypeName')">{{
              bulkHistoryDetail.bulkTypeName | formatEmpty
            }}</el-descriptions-item>
            <el-descriptions-item :label="$t('common.label.status')">
              {{ bulkStatusMap[bulkHistoryDetail.bulkStatus] | formatEmpty }}
            </el-descriptions-item>
            <el-descriptions-item :label="$t('BUC.taskPlan.createdBy')">{{
              bulkHistoryDetail.createdBy | formatEmpty
            }}</el-descriptions-item>
            <el-descriptions-item :label="$t('BUC.taskPlan.approvedBy')">{{
              bulkHistoryDetail.approveOperId | formatEmpty
            }}</el-descriptions-item>
            <el-descriptions-item :label="$t('BUC.taskHistory.dataFile')">
              <div v-if="bulkHistoryDetail.dataFileName">
                <span>{{ bulkHistoryDetail.dataFileName }}</span>
                <svg-icon
                  @click="downloadBulkFile('data')"
                  icon-class="bulkDownload"
                  class="bulkDownload-icon"
                ></svg-icon>
              </div>
              <span v-else>-</span>
            </el-descriptions-item>
            <el-descriptions-item :label="$t('BUC.taskHistory.creationTime')">{{
              formatDateTimeStr3(bulkHistoryDetail.createTime) | formatEmpty
            }}</el-descriptions-item>
            <el-descriptions-item :label="$t('BUC.taskHistory.scheduleTime')">{{
              formatDateTimeStr3(bulkHistoryDetail.scheduleTime) | formatEmpty
            }}</el-descriptions-item>
            <el-descriptions-item :label="$t('BUC.taskHistory.reportFile')">
              <span v-if="bulkHistoryDetail.reportFileName">{{ bulkHistoryDetail.reportFileName }}</span>
              <el-dropdown v-if="bulkHistoryDetail.reportFileName" @command="downloadBulkFile" trigger="click">
                <span class="el-dropdown-link"
                  ><svg-icon icon-class="bulkDownload" style="margin-left: 0" class="bulkDownload-icon"></svg-icon
                  ><i class=".el-icon-a-BulkTaskTemplateDownload el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item
                    v-for="item in bulkHistoryDetail.reportList"
                    :key="item.docID"
                    :command="item.docID"
                    >{{ item.fileFormat }}</el-dropdown-item
                  >
                </el-dropdown-menu>
              </el-dropdown>
              <span v-else>-</span>
            </el-descriptions-item>
            <el-descriptions-item :label="$t('common.label.description')">{{
              bulkHistoryDetail.description | formatEmpty
            }}</el-descriptions-item>
          </el-descriptions>
        </div>
      </el-card>
      <!-- Basic Info -->

      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <div class="title">{{ $t('BUC.taskHistory.itemCheckResult') }}</div>
        </div>
        <div class="text item">
          <el-descriptions
            :column="descriptionsColumn"
            direction="vertical"
            :colon="false"
            :label-style="labelStyle"
            :content-style="contentStyle"
          >
            <el-descriptions-item :label="$t('BUC.taskHistory.totalUploadedItems')">{{
              bulkHistoryDetail.numberOfTotal
            }}</el-descriptions-item>
            <el-descriptions-item :label="$t('BUC.taskHistory.passedValidationItems')">
              <el-link
                v-if="bulkHistoryDetail.numberOfValidSuccess >= 0"
                @click="viewDetailDialog('passedValidationItems')"
                :underline="false"
                type="primary"
              >
                {{ bulkHistoryDetail.numberOfValidSuccess }}
              </el-link>
              <span v-else>-</span>
            </el-descriptions-item>
            <el-descriptions-item :label="$t('BUC.taskHistory.failedValidationItems')">
              <el-link
                v-if="bulkHistoryDetail.numberOfValidFailed >= 0"
                @click="viewDetailDialog('failedValidationItems')"
                :underline="false"
                type="primary"
              >
                {{ bulkHistoryDetail.numberOfValidFailed }}
              </el-link>
              <span v-else>-</span>
            </el-descriptions-item>
          </el-descriptions>
        </div>
      </el-card>

      <!-- Execution Info -->
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <div class="title">{{ $t('BUC.taskHistory.executionInfo') }}</div>
        </div>
        <div class="text item execution-info-item">
          <div>
            <el-descriptions
              :column="descriptionsColumn"
              direction="vertical"
              :colon="false"
              :label-style="labelStyle"
              :content-style="contentStyle"
            >
              <el-descriptions-item :label="$t('BUC.taskHistory.startTime')">{{
                formatDateTimeStr3(bulkHistoryDetail.createTime) | formatEmpty
              }}</el-descriptions-item>
              <el-descriptions-item :label="$t('BUC.taskHistory.finishTime')">{{
                formatDateTimeStr3(bulkHistoryDetail.scheduleTime) | formatEmpty
              }}</el-descriptions-item>
              <el-descriptions-item :label="$t('BUC.taskTemplate.reasonType')">{{
                formatDateTimeStr3(bulkHistoryDetail.reasonType) | formatEmpty
              }}</el-descriptions-item>
            </el-descriptions>
          </div>
          <!-- colums -->
          <div class="content">
            <table-page
              ref="executionInfoTable"
              size="small"
              :has-pagination="true"
              :columns="columns"
              :outSideTableData="outSideTableData"
            >
              <template slot="successItems" slot-scope="scope">
                <el-link @click="viewDetailDialog('successItems')" :underline="false" type="primary">{{
                  scope.data.row.successItems
                }}</el-link>
              </template>
              <template slot="failedItems" slot-scope="scope">
                <el-link @click="viewDetailDialog('failedItems')" :underline="false" type="primary">
                  {{ scope.data.row.failedItems }}</el-link
                >
              </template>
              <template slot="executionRate" slot-scope="scope">
                <el-progress
                  :percentage="scope.data.row.executionRate"
                  :color="`${GlobalStyleVariables.primaryColor}`"
                ></el-progress>
              </template>
            </table-page>
          </div>
        </div>
      </el-card>
      <div class="page-bottom-box" v-if="routerType === 'process'">
        <div class="margin-right-20">
          <el-button
            id="cancel"
            :loading="cancelLoading"
            class="cancel-button"
            size="small"
            @click="handleCancelClick()"
            >{{ $t('common.button.cancel') }}
          </el-button>
          <el-button
            id="resume"
            :disabled="bulkHistoryDetail.bulkStatus !== 'SUSPEND'"
            size="small"
            type="primary"
            @click="handleResumeClick"
            :loading="resumeLoading"
          >
            {{ $t('BUC.taskHistory.resume') }}
          </el-button>
          <el-button
            :disabled="bulkHistoryDetail.bulkStatus !== 'PROCESSING'"
            id="suspend"
            size="small"
            type="primary"
            v-preventReClick
            @click="handleSuspendClick"
            :loading="suspendLoading"
          >
            {{ $t('BUC.taskHistory.suspend') }}
          </el-button>
        </div>
      </div>
      <portal-dialog
        :visible.sync="dialogVisible"
        :title="title"
        :cancelText="$t('common.button.close')"
        :width="'800px'"
        :height="'442px'"
        @cancel-btn-click="handleClose"
        :before-close="handleClose"
        :showSubmit="false"
        :showCancel="false"
      >
        <template #content>
          <table-page
            ref="dialogTable"
            size="small"
            :has-pagination="true"
            :columns="dialogColumns"
            :pagination="dialogPaginationConfig"
            :getTableData="dialogGetTableData"
            :is-search="refreshTableFlag"
          >
            <template slot="processTime" slot-scope="scope">
              {{ formatDateTimeStr(scope.data.row.processTime) }}
            </template>
          </table-page>
        </template>
      </portal-dialog>
    </div>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';
import PortalHeader from '@/components/portal-header';
import PortalDialog from '@/components/portal-dialog';
import { formatDateTimeStr3, formatDateTimeStr } from '@/common/utils/date-time-utils';
import { mapGetters } from 'vuex';
import {
  getFailedRecords,
  queryValidRecords,
  getSuccessRecords,
  downloadBulkFile,
  suspendTaskPlan,
  resumeTaskPlan,
  cancelTaskPlan,
  getTaskPlanDetail,
  queryBulkHistoryDetail,
  queryBulkDataDicts,
} from '../../../api/api-bulk';
import TopNavigation from '../components/top-navigation.vue';
export default {
  components: { TablePage, PortalHeader, PortalDialog, TopNavigation },
  name: 'BulkTaskHistoryView',
  data() {
    return {
      intervalId: '',
      isShowNavigation: false,
      routerType: '',
      resumeLoading: false,
      suspendLoading: false,
      dialogVisible: false,
      dialogOperMode: '',
      refreshTableFlag: false,
      outSideTableData: [],
      searchLoading: false,
      isDisableDelete: true,
      bulkHistoryDetail: {},
      cancelLoading: false,
      labelStyle: {
        'font-size': '14px',
        color: `${this.GlobalStyleVariables.lineStrongColor}`,
        'font-weight': '400',
        padding: '0 10px 5px 10px',
      },
      contentStyle: {
        'font-size': '14px',
        color: `${this.GlobalStyleVariables.textEmphasisColor}`,
        'font-weight': '400',
        padding: '0 10px 20px 10px',
      },
      dialogType: '',
      columns: [
        {
          label: `${this.$t('BUC.taskHistory.successItems')}`,
          prop: 'successItems',
          align: 'left',
          __slotName: 'successItems',
        },
        {
          label: `${this.$t('BUC.taskHistory.failedItems')}`,
          prop: 'failedItems',
          align: 'left',
          __slotName: 'failedItems',
        },
        { label: `${this.$t('BUC.taskHistory.processingItems')}`, prop: 'processingItems', align: 'left' },
        { label: `${this.$t('BUC.taskPlan.waitingItems')}`, prop: 'waitingItems', align: 'left' },
        {
          label: `${this.$t('BUC.taskHistory.executionRate')}`,
          prop: 'executionRate',
          align: 'left',
          __slotName: 'executionRate',
        },
      ],
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 5,
        layout: 'total,prev, pager, next',
      },
      dialogPaginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 5,
        layout: 'total,prev, pager, next',
      },
      titleConfig: {
        title: this.$t('BUC.taskPlan.bulkTaskView'),
      },
      descriptionsColumn: 3,
      screenWidth: null,
    };
  },
  watch: {
    screenWidth: {
      handler: function (val) {
        if (val < 1920) {
          this.descriptionsColumn = 3;
        } else {
          this.descriptionsColumn = 4;
        }
      },
      immediate: true,
    },
    'bulkHistoryDetail.bulkStatus': {
      handler(value) {
        if (value === 'PROCESSING') {
          this.setSearchInterval();
        } else {
          this.clearSearchInterval();
        }
      },
      immediate: true,
    },
  },
  computed: {
    ...mapGetters('datadict', ['moduleDict']),
    bulkStatusMap() {
      let hisStatus = this.moduleDict.bulk['config.bulk.his.status.list'].objMap;
      let status = this.moduleDict.bulk['config.bulk.plan.status.list'].objMap;
      return Object.assign(hisStatus, status);
    },
    dialogColumns() {
      switch (this.dialogOperMode) {
        case 'passedValidationItems':
          return [
            { label: `Number`, prop: 'lineNo', align: 'left' },
            { label: `Summary`, prop: 'summary', align: 'left' },
          ];
        case 'failedValidationItems':
          return [
            { label: `Number`, prop: 'lineNo', align: 'left' },
            { label: `Summary`, prop: 'summary', align: 'left' },
            { label: `Execution Time`, prop: 'processTime', align: 'left', __slotName: 'processTime' },
            { label: `Error Message`, prop: 'errorDesc', align: 'left' },
            { label: `Cause`, prop: 'errorCause', align: 'left' },
            { label: `Solution`, prop: 'errorSolution', align: 'left' },
          ];
        case 'successItems':
          return [
            { label: `Number`, prop: 'lineNo', align: 'left' },
            { label: `Summary`, prop: 'summary', align: 'left' },
            { label: `Execution Time`, prop: 'processTime', align: 'left', __slotName: 'processTime' },
          ];
        case 'failedItems':
          return [
            { label: `Number`, prop: 'lineNo', align: 'left' },
            { label: `Summary`, prop: 'summary', align: 'left' },
            { label: `Execution Time`, prop: 'processTime', align: 'left', __slotName: 'processTime' },
            { label: `Error Message`, prop: 'errorDesc', align: 'left' },
            { label: `Cause`, prop: 'errorCause', align: 'left' },
            { label: `Solution`, prop: 'errorSolution', align: 'left' },
          ];
        default:
          return [];
      }
    },
    title() {
      switch (this.dialogOperMode) {
        case 'passedValidationItems':
          return 'Details of Passed Initial Validation Items';
        case 'failedValidationItems':
          return 'Details of Failed Initial Validation Items';
        case 'successItems':
          return 'Details of Success Items';
        case 'failedItems':
          return 'Details of Failed Items';
        default:
          return '';
      }
    },

    /**
     *@param validLevel
     * 校验级别,多个用逗号隔开
     * ERROR("0", "Error"),
     * INVALID("1", "Invalid"), 无效:数据不合法
     * NORMAL("2", "Normal"); // 正常
     */
    dialogGetTableData() {
      switch (this.dialogOperMode) {
        case 'passedValidationItems': {
          let apiFn = queryValidRecords;
          let params = {
            bulkFileID: this.bulkHistoryDetail.fileID,
            finishTime: this.bulkHistoryDetail.finishTime,
            validLevel: 2,
            isHis: 1,
          };
          if (this.routerType === 'process') {
            params.isHis = '0';
            params.finishTime = this.bulkHistoryDetail.finishTime;
          }
          let getTableDataFn = this.getDialogTableData(apiFn, params);
          return getTableDataFn;
        }
        case 'failedValidationItems': {
          let apiFn = queryValidRecords;
          let params = {
            bulkFileID: this.bulkHistoryDetail.fileID,
            validLevel: 0,
            isHis: 1,
          };
          if (this.routerType === 'process') {
            params.isHis = '0';
            params.finishTime = this.bulkHistoryDetail.finishTime;
          }
          let getTableDataFn = this.getDialogTableData(apiFn, params);

          return getTableDataFn;
        }
        case 'successItems': {
          let apiFn = getSuccessRecords;
          let getTableDataFn = this.getDialogTableData(apiFn, {
            bulkPlanID: this.bulkHistoryDetail.planID,
            finishTime: this.bulkHistoryDetail.finishTime,
            isHis: this.routerType === 'process' ? '0' : '1',
          });
          return getTableDataFn;
        }
        case 'failedItems': {
          let apiFn = getFailedRecords;
          let getTableDataFn = this.getDialogTableData(apiFn, {
            bulkPlanID: this.bulkHistoryDetail.planID,
            finishTime: this.bulkHistoryDetail.finishTime,
            isHis: this.routerType === 'process' ? '0' : '1',
          });
          return getTableDataFn;
        }
        default:
          return '';
      }
    },
  },
  methods: {
    formatDateTimeStr,
    formatDateTimeStr3,
    reset() {
      this.paginationConfig.pageIndex = 1;
      this.refreshTableFlag = true;
    },
    setSearchInterval() {
      if (!this.intervalId) {
        this.intervalId = setInterval(() => {
          this.getRouteParam();
          this.refreshTableFlag = true;
        }, 5000);
      }
    },
    clearSearchInterval() {
      if (this.intervalId) {
        clearInterval(this.intervalId);
        this.intervalId = '';
      }
    },
    handleSuspendClick() {
      this.$confirm(
        `Are you sure to suspend this task plan "${this.bulkHistoryDetail.planName}" ?`,
        'Suspend Task Plan',
        {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          closeOnClickModal: false,
        }
      ).then(() => {
        this.suspendLoading = true;
        suspendTaskPlan({ planIDs: [this.bulkHistoryDetail.planID] })
          .then((res) => {
            if (res.header.success) {
              this.$modal.msgSuccess('Suspend successfully.');
              this.$router.push({
                path: '../../bulk/bulkTask/list',
              });
            }
          })
          .catch()
          .finally(() => {
            this.suspendLoading = false;
          });
      });
    },
    handleResumeClick() {
      this.$confirm(
        `Are you sure to resume this task plan "${this.bulkHistoryDetail.planName}" ?`,
        'Resume Task Plan',
        {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          closeOnClickModal: false,
        }
      ).then(() => {
        this.resumeLoading = true;
        resumeTaskPlan({ planIDs: [this.bulkHistoryDetail.planID] })
          .then((res) => {
            if (res.header.success) {
              this.$modal.msgSuccess('Resume successfully.');
              this.$router.push({
                path: '../../bulk/bulkTask/list',
              });
            }
          })
          .catch()
          .finally(() => {
            this.resumeLoading = false;
          });
      });
    },
    handleCancelClick(closeDialog) {
      if (!closeDialog) {
        this.$confirm('Are you sure to cancel this plan?', {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          closeOnClickModal: false,
          type: 'warning',
        }).then(() => {
          this.cancelLoading = true;
          let params = {
            bulkPlanID: this.bulkHistoryDetail.planID,
          };
          cancelTaskPlan(params)
            .then((res) => {
              if (res.header.success) {
                this.$modal.msgSuccess('Cancel successfully.');
                this.$router.push({
                  path: '../../bulk/bulkTask/list',
                });
              }
            })
            .finally(() => {
              this.cancelLoading = false;
            });
        });
      }
    },

    handleClose() {
      this.dialogVisible = false;
      this.dialogPaginationConfig = {
        total: 0,
        pageIndex: 1,
        pageSize: 5,
      };
    },
    downloadBulkFile(fileType) {
      if (fileType == 'data') {
        downloadBulkFile(this.bulkHistoryDetail.fileDocID, 1);
      } else {
        downloadBulkFile(fileType, 2);
      }
    },
    viewDetailDialog(dialogOperMode) {
      this.dialogVisible = true;
      this.dialogOperMode = dialogOperMode;
      this.refreshTableFlag = true;
    },
    getRouteParam() {
      let params = this.$route.query.params;
      let paramsObj = params && JSON.parse(decodeURIComponent(params));
      if (paramsObj.isShowNavigation) {
        this.isShowNavigation = true;
      }
      this.routerType = paramsObj.type;
      this.bulkHistoryDetail = paramsObj;
      let api = getTaskPlanDetail;
      let detailsParams = {
        planID: paramsObj.bulkPlanID,
      };
      if (this.routerType === 'details') {
        api = queryBulkHistoryDetail;
        detailsParams.finishTime = paramsObj.finishTime;
      }
      api(detailsParams).then((res) => {
        let { bulkResultInfo, basicBulkPlanInfo } = res.data.bulkPlanInfo;
        this.bulkHistoryDetail = { ...basicBulkPlanInfo, ...bulkResultInfo };
        let successItems = bulkResultInfo.numberOfExecuteSuccess;
        let failedItems = bulkResultInfo.numberOfExecuteFailed;
        let processingItems = bulkResultInfo.numberOfProcessingItems;
        let totalItems = bulkResultInfo.numberOfTotal;
        let executionRate = 0;
        if (totalItems * 1 === 0 || successItems * 1 === 0) {
          executionRate = 0;
        } else {
          executionRate = Math.floor(((successItems * 1) / (totalItems * 1)) * 100);
        }
        this.outSideTableData = [
          {
            successItems,
            failedItems,
            processingItems,
            executionRate: executionRate,
          },
        ];
      });
    },
    async initDataDict() {
      this.$store.dispatch('datadict/getModuleDicts', {
        moduleName: 'bulk',
        dictCodes: ['config.bulk.plan.status.list', 'config.bulk.his.status.list'],
        queryAPI: queryBulkDataDicts,
      });
    },
    getDialogTableData(apiFn, parameter) {
      return () => {
        return new Promise((resolve) => {
          let { pageSize, pageIndex } = this.dialogPaginationConfig;
          let paginationConfig = {
            limit: pageSize,
            offset: (pageIndex - 1) * pageSize,
          };
          apiFn({ ...parameter, ...paginationConfig })
            .then((res) => {
              if (res.header.success) {
                resolve({
                  header: res.header,
                  totalRecords: res.data.numberOfTotal,
                  list: res.data.bulkDataInfoList || [],
                });
              }
            })
            .catch(() => {
              resolve();
            })
            .finally(() => {
              this.refreshTableFlag = false;
            });
        });
      };
    },
    showStatus(val) {
      let backgroundColor = this.GlobalStyleVariables.backgroundColor;
      let textColor = this.GlobalStyleVariables.fillEmphasisColor;
      switch (val) {
        case 'Checking':
        case 'Initial':
          backgroundColor = this.GlobalStyleVariables.backgroundColor;
          textColor = this.GlobalStyleVariables.fillEmphasisColor;
          break;
        case 'Processing':
          backgroundColor = this.GlobalStyleVariables.statusNoticeBackgroundColor;
          textColor = this.GlobalStyleVariables.statusNoticeColor;
          break;
        case 'Suspend':
        case 'Wait Process':
        case 'Wait Active':
          backgroundColor = this.GlobalStyleVariables.lineColorWarning;
          textColor = this.GlobalStyleVariables.statusColorwarning;
          break;
        case 'Dis Active':
        case 'Canceled':
        case 'Deactive':
        case 'Expire':
          backgroundColor = this.GlobalStyleVariables.statusDarkBackgroundColor;
          textColor = this.GlobalStyleVariables.fillStrongColor;
          break;
        case 'Failed':
          backgroundColor = this.GlobalStyleVariables.statusErrorBackgroundColor;
          textColor = this.GlobalStyleVariables.statusErrorColor;
          break;
        case 'Completed':
          backgroundColor = this.GlobalStyleVariables.statusSuccessBackgroundColor;
          textColor = this.GlobalStyleVariables.statusSuccessColor;
          break;
        default:
          break;
      }
      return `background-color:${backgroundColor};color:${textColor}`;
    },
  },
  mounted() {
    this.screenWidth = document.body.clientWidth;
    window.onresize = () => {
      return (() => {
        this.screenWidth = document.body.clientWidth;
      })();
    };
  },
  created() {
    this.initDataDict();
    this.refreshTableFlag = true;
  },
  activated() {
    this.getRouteParam();
  },
  deactivated() {
    this.clearSearchInterval();
  },
  beforeDestroy() {
    this.clearSearchInterval();
  },
};
</script>

<style scoped lang="scss">
.project-detail {
  /deep/.el-dialog {
    border-radius: 4px;
  }
  .section {
    padding: 0 10px;
    .title {
      font-size: 16px;
      color: $--text_color--emphasis;
      padding: 0 0 20px 10px;
      font-weight: bold;
    }
  }
  .el-link {
    justify-content: flex-start;
    text-decoration: underline;
  }
  .el-divider--horizontal {
    margin: 0 0 40px;
  }

  .content {
    border-radius: 0 0 4px 0;
  }
  /deep/ .el-progress-bar__outer {
    height: 3px !important;
  }
}
.version-box {
  display: inline-block;
  text-align: center;
  width: 78px;
  height: 28px;
  line-height: 28px;
  font-size: 14px;
  position: relative;
  bottom: 3px;
  span {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 95px;
    height: 28px;
    border-radius: 4px;
  }
}
.el-dropdown-link {
  margin-left: 10px;
  color: $--primary_color;
