<template>
  <div class="top-navigation">
    <span class="navigation">
      <svg-icon icon-class="left_small" @click="backTo" class="arrow-icon"></svg-icon>
      <span class="previous-page" @click="backTo">{{ previousPage }} </span>
      <span class="previous-page"> / </span>
      <span class="current-page">{{ currentPage }}</span>
    </span>
  </div>
</template>
<script>
export default {
  name: 'TopNavigation',
  props: {
    currentPage: {
      type: String,
      default: '',
    },
    previousPage: {
      type: String,
      default: '',
    },
    isOperationForm: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {};
  },
  methods: {
    backTo() {
      if (this.isOperationForm) {
        this.$confirm('Are you sure to return to the Bulk Task?', {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          closeOnClickModal: false,
          type: 'warning',
        }).then(() => {
          this.$router.go(-1);
        });
      } else {
        this.$router.go(-1);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.top-navigation {
  margin-bottom: 14px;
}
.navigation {
  span {
    font-size: 14px;
    font-weight: 400;
  }
  position: relative;
  height: 14px;
  color: $--plain_color--level3;
  cursor: pointer;
  .arrow-icon {
    color: $--text_color--secondary;
  }
  .previous-page {
    color: $--text_color--gray;
    font-style: normal;
  }
  .current-page {
    font-style: normal;
    color: $--text_color--strong;
  }
}
</style>
